@font-face {
  font-family: Avenir Book;
  src: url("Avenir-Book.657c3568.ttf");
  font-weight: 300;
}

@font-face {
  font-family: Avenir Heavy;
  src: url("Avenir-Heavy.01d9ab74.ttf");
  font-weight: 900;
}

html {
  --text-color: #3d4e61;
  --border-color: #e1e7ed;
  --default-shadow-color: #3d4e611a;
  --hover-shadow-color: #00000040;
}

.search-bar {
  box-sizing: border-box;
  color: var(--text-color);
  border: 1px solid var(--border-color);
  box-shadow: 0 1px 8px 0 var(--default-shadow-color);
  border-radius: 4px;
  outline: none;
  width: 100%;
  font-family: Avenir Book, sans-serif;
}

.search-bar:hover {
  box-shadow: 0 4px 4px 0 var(--hover-shadow-color);
}

.search-bar:focus {
  text-shadow: 0 4px 4px var(--hover-shadow-color);
  background: linear-gradient(#fff 0%, #f6f6f7 100%);
  font-family: Avenir Heavy, sans-serif;
}

.search-bar--big {
  height: 70px;
  padding-left: 62px;
  font-size: 16px;
  line-height: 21.86px;
}

.search-bar--small {
  height: 42px;
  padding-left: 33px;
  font-size: 14px;
  line-height: 19.12px;
}

.form {
  margin-top: 20px;
  position: relative;
}

.form:before {
  content: "";
  z-index: 1;
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
}

.form--big:before {
  width: 19px;
  height: 19px;
  top: 25px;
  left: 26px;
}

.form--small:before {
